.container-foods-page {
  background-color: #FFF4F4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 50px;
}

.recipes-title {
  font-size: 13px;
  margin: 0;
  line-height: 15px;
  color: #FF6060;
  text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25)
}

.container-recipes {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 20px 0;
}

.box-recipes {
  display: flex;
  flex-direction: column;
  width: 100%;
}
